import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'
import UserTable from './views/user-manager/UserTable.vue'
import BatchManager from  './views/user-manager/BatchManager.vue'
import RolePermission from './views/user-manager/RolePermission.vue'
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },

    //{ path: '/main', component: Main },
    {
       path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',
        leaf: true,
        children: [
            {path: '/', component: Main , name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
           // { path: '/', component: Main, name: '', hidden: false },
            { path: '/user_info', component: UserTable, name: '' },
            { path: '/batch_manager', component: BatchManager, name: '' },
            { path: '/RolePermission', component: RolePermission, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/page4', component: Table, name: '' },
            {
                path: '/page5', component: Page5, name: '',
                children:[
                    {
                        path: '/', component:Page4 , name: ''
                    },
                    {
                        path: '/edemed' ,component: UserTable , name: ''
                    }
                ]
            },
            { path: '/page6', component: Page5, name: '' }

        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: false,//
        children: [
            { path: '/page7', component: Page6, name: '6' },
            { path: '/page8', component: Table, name: '5' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: false,//
        children: [
            { path: '/page9', component: Page6, name: '4' },
            { path: '/page10', component: Table, name: '3' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: false,//
        children: [
            { path: '/page11', component: Page6, name: '1' },
            { path: '/page12', component: Table, name: '2' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    /*//
    {
        path: '/page5',
        component: Home,
        name: '2',
        hidden:true,
        children: [
            {path: '/RolePermission1', component: UserTable , name: '3'}
        ]
    },*/
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },

];

export default routes;


